/*carousel 1*/
.slider-wrapper {
    background-color: black;
  }
.slider-container {
    position: relative;
    text-align: center;
    max-width: 1000px;
    margin: auto;
  }
  
  .slider {
    overflow: hidden;
  }
  
  .slide {
    display: none;
  }
  
  .slide.active {
    display: block;
  }
  
  .slide-image-container {
    margin: auto;
    position: relative;
    max-width: 400px;
    height: auto;
    border-radius: 50px;
  }
  
  .slide-image {
    width: 100%;
    height: auto;
    padding-top: 25px;
    padding-bottom: 10px;
    display: block;
    border-radius: 100px;
    transition: transform 0.5s ease;
  }
  
  
  .slide-text {
    margin-top: 1rem;
  }
  
  .slide-heading {
    font-size: 2rem;
    font-weight: bold;
    color: #7efa91;
    font-family: Arial, sans-serif;
    text-align: center;
  }
  
  .slide-description {
    margin-top: 0.5rem;
    margin-bottom: 70px;
    font-size: 1.5rem;
    color: #D70081;
    font-family: Arial, sans-serif;
    text-align: center;
  }
  
  .slider-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgb(187, 186, 186);
    border: none;
    border-radius: 500px;
    font-size: 2rem;
    cursor: pointer;
    z-index: 1;
    padding: 10px;
  }
  
  .prev {
    left: 0;
  }
  
  .next {
    right: 0;
  }
  
  .sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
  }
  /* Tablet Screen (641px to 1024px) */
@media (max-width: 1024px) {
  .slider-wrapper {
    width: auto;
    overflow-x: hidden;
  }
  .slide-image-container {
    margin: auto; /* Adjust margin for tablets */
  }

  .slide-heading {
      font-size: 2rem; /* Smaller font size for tablet */
  }

  .slide-description {
      font-size: 1.5rem; /* Smaller font size for better fit */
  }
}

/* Mobile Screen (up to 640px) */
@media (max-width: 640px) {
  .slider-wrapper {
    width: auto;
    overflow-x: hidden;
  }
  .slide-image-container {
    margin: auto; 
  }
  .slide-image {
      max-width: 100px; 
  }

  .slide-heading {
      font-size: 1.2rem; 
  }

  .slide-description {
      font-size: 1rem; 
      margin-bottom: 30px; 
  }

  .slider-button {
      font-size: 1rem; 
      padding: 0; 
      background-color: transparent;
      color: white;
  }

  .prev, .next {
      width: 40px; 
      height: 40px; 
  }
}



  

  /* artclub section */
.art-club-container {
    width: auto;
    padding: 0 15px;
    margin: 0 auto;
    background-color: #d70081;
    height: 100vh;
  }
  
  .art-club-row {
    display: flex;
    justify-content: center;
    margin-right: -15px;
    margin-left: -15px;
  }
  
  .art-club-card {
    width: 100%;
    max-width: 1000px;
    margin: 20px 0;
  }
  
  .art-club-card-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .art-club-card-box {
    text-align: center;
    padding: 30px;
    border: solid;
    background-color: black;
    border-radius: 30px;
    width: 65%;
    height: 500px;
    margin: 20px;
  }
  
  .art-club-card-title {
    font-size: 4rem;
    font-weight: bold;
    color: rgb(233, 233, 233);
    font-family: Arial, sans-serif;
  }
  
  .art-club-text {
    font-size: 1.5rem;
    margin-bottom: 6rem;
    color: #6bf38bfd;
    font-family: Arial, sans-serif;
  }
  
  .art-club-section-btn {
    margin-top: 3rem;
  }
  
  .artclub-btn {
    padding: 25px 30px;
    font-size: 1rem;
    text-decoration: none;
    border: 2px solid #fff;
    border-radius: 50px;
    transition: background-color 0.3s ease;
    color: #fff;
    background-color: transparent;
  }
  
  .artclub-btn:hover {
    color: rgba(245, 85, 160, 0.774); 
    border-color: rgb(245, 85, 160, 0.774);
  }
  /* Tablet Screen (641px to 1024px) */
@media (max-width: 1024px) {
  .art-club-container {
    width: auto;
    overflow-x: hidden;
  }
  .art-club-card-title {
      font-size: 3rem; 
  }

  .art-club-text {
      font-size: 1.2rem; 
      margin-bottom: 4rem; 
  }
  .art-club-card-box {
    padding: 20px;
    max-width: 100%;
    height: auto; 
    padding-bottom: 100px;
}
}
/* Mobile Screen (up to 640px) */
@media (max-width: 640px) {
  .art-club-container {
      width: auto;
      overflow-x: hidden;
  }

  .art-club-card-box {
      padding: 20px;
      max-width: 100%;
      height: auto; 
      padding-bottom: 100px;
  }

  .art-club-card-title {
      font-size: 2rem; /* Much smaller font size for mobile */
  }

  .art-club-text {
      font-size: 1rem; /* Smaller text size */
      margin-bottom: 2rem; /* Reduced bottom margin */
  }


  .artclub-btn {
      padding: 10px 15px; /* Smaller padding */
      font-size: 1rem; /* Smaller font size */
      margin-bottom: 20px;
      
  }
  .art-club-section-btn {
    margin-top: 30px;
    
