/*carousel 2*/
.slider2{
  background-color: black;
  width: auto;
  height: 150vh;
}
.slider2-header{
  text-align: center;
  padding: 20px;
}
.slider2-header h3{
  color: #D70081;
  font-family: Arial, sans-serif;
  font-weight: bold;
  font-size: 3.5rem;
}
.slider2-header p{
  color: #7efa91;
