  font-size: 2.5rem;
  text-align: center;
}
slider2-container {
  position: relative;
  width: 100%;
  overflow: hidden;
  height: 100vh;
}

.embla {
  overflow: hidden;
  width: 100%;
  height: auto;
}
.embla__viewport {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.embla__container {
  display: flex;
  will-change: transform;
}

.event-card {
  position: relative;
  flex: 0 0 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: white;
}
.event-card__image-wrap{
  position: relative;
  flex: 0 0 100%;
  height: auto; /* Adjust based on your preference */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 5px;
}
.event-card__image-wrap img {
  width: 450px; /* This makes images responsive */
  height: 450px; /* Maintain aspect ratio */
  display: block;
}

.carousel-button-prev, .carousel-button-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 10px 20px;
  color: white;
  background-color: #333;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  z-index: 10;
}

.carousel-button-prev {
  left: 10px;
  margin-left: 30px;
}

.carousel-button-next {
  right: 10px;
  margin-right: 30px;
}

.carousel-button-prev:hover, .carousel-button-next:hover {
  background-color: #555;
}

@media (max-width: 768px) {
  carousel-button-prev, .carousel-button-next:hover {
    font-size: 14px;
    padding: 8px 16px;
  }

  event-card__image-wrap img {
    max-height: 70%; /* Smaller images on smaller screens */
  }
}








